.alio-btn:after {
  content: "";
  position: absolute;
  transition: all 0.2s ease-out;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg) translateZ(0);
  height: 0;
  width: 0;
  opacity: 0;
  background-color: #541737; }

.alio-btn:hover i, .alio-btn:hover span {
  color: #FFF;
  transform: translateX(0);
  opacity: 1; }

.alio-btn:focus {
  outline: none;
  text-decoration: none;
  color: #fff; }
  .alio-btn:focus span {
    color: #fff; }

.alio-btn .in-button {
  display: inline-block;
