.alio-logo {
    float: right;
    padding: 5px 0 11px 0;
}

.navbar-toggle {
    background: #4c74af;
    background: -webkit-linear-gradient(top,  #978ebb,  #4c74af);
    background: -moz-linear-gradient(top,  #978ebb,  #4c74af);
    background: -o-linear-gradient(top,  #978ebb,  #4c74af);
    background: -ms-linear-gradient(top,  #978ebb,  #4c74af);
    background: linear-gradient(to bottom,  #978ebb,  #4c74af);
}

.navbar-toggle span {
    background: #fff;
}

.alio-navbar {
    display: inline-block;

    .dropdown-menu {
        background-color: $lightBlue;
        font-weight: 400;
        min-width: auto;
        right: 0;
        left: auto;

        a {
            border-bottom: 1px solid #e8e5e5;
            padding-top: 9px;
            padding-bottom: 9px;
            color: #333333;
            font-family: 'Roboto Condensed Regular',sans-serif;
            font-weight: 400;
            font-size: 13px;
            border: 0;
            padding: 7px 20px;
            line-height: 19px;
            text-indent: 0;
            height: auto;
            display: block;
            box-sizing: content-box;
        }
    }
}

.dropdown-menu > .active > a, .dropdown-menu > .active > a:hover, .dropdown-menu > .active > a:focus {
    color: $superPurple;
    background-color: transparent;
    background-image: none;
}

.alio-navbar > li {
    display: inline-block;
}

.alio-navbar li a {
    display: inline-block;
    color: $superPurple;
    font-size: 14px;
    font-weight: 700;
    line-height: 19px;
    padding: 0;
    text-transform: uppercase;
}

.alio-navbar li.active a, .alio-navbar li a:hover {
    color: $blue;
    text-decoration: none;
    background-color: transparent;
    background-image: none;
}

.alio-navbar li i {
    border-right: 1px solid #dfe8ed;
    display: block;
    height: 29px;
    left: 100%;
    position: absolute;
    top: 0;
    width: 3px;
}

.navbar {
    border: none;
    width: 100%;
    position: relative;

    .alio-btn {
        float: right;
        margin: 3px 12px;
        padding: 15px 15px 12px;
        font-size: 13px;
    }

    .nav-design {
        padding: 10px 15px 10px;
        margin-left: auto;
        margin-right: auto;
        background: $lightBlue;
        box-shadow: 0 0 10px rgba(0,0,0,.5);
        position: relative;
        border-radius: 40px 40px 20px 20px;

        &:before, &:after {
            content: '';
            display: block;
            position: absolute;
            width: 44px;
            height: 60px;
            bottom: -7px;
            border-radius: 90px;
            background: $lightBlue;
            z-index: 0;
        }

        &:before {
            left: 5px;
            transform: rotate(35deg);
        }

        &:after {
            right: 5px;
            transform: rotate(-35deg);
        }
    }

    .nav-holder {
        position: relative;
        z-index: 1;
    }

    &.sticky {
        position: fixed;
        top: 0;
        z-index: 1000;
    }
}

.navbar:not(.sticky) {
    .alio-btn {
        display: none;
    }
}

.admin-bar .navbar.sticky {
    top: 32px;
}

.language-chooser {
    li {

        a {
            color: #fff;
            background-position: right center;
        }

        span {
            padding-right: 23px;

            &:before {
                content: '\f111';
                font-family: "Font Awesome 5 Pro";
                font-weight: bold;
                margin-right: 5px;
            }
        }
    }

    li.active {
        span {
            &:before {
                content: '\f192';
            }
        }
    }
}

body.home .navbar {
    li.menu-item-238 {
        display: none;
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    100% {
        opacity: 0.9;
        transform: translateY(0);
    }
}

.navbar.sticky {
    animation: slide-down 0.7s;
}

.navbar-collapse {
    padding-right: 0;
}

#primary-navbar-collapse {
    text-align: right;
}

.navbar .bg-navbar {
    left: 0;
    position: absolute;
    bottom: -9px;
    width: 100%;
}

.navbar .bg-navbar div {
    background-image: url("#{$img-path}/bg-nav-bottom2.png");
    background-position: 100% -16px;
    background-repeat: repeat-x;
    height: 10px;
    width: 100%;
}

.navbar.sticky .bg-navbar {
    top: 100%;
}

.alio-navbar > li > a:after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background: $blue;
    transform: scale(0,1);
    transform-origin: left center;
    transition: all .15s ease-out;
    filter: alpha(opacity=100);
    opacity: 0;
}

.alio-navbar > li:hover > a:after {
    transform: scale(1,1);
    filter: alpha(opacity=100);
    opacity: 1;
}

.alio-navbar > li > a:hover, .alio-navbar.nav > li > a:focus {
    background-color: transparent;
    color: $blue;
    text-decoration: none;
}

#searchform {
    position: relative;
    float: right;
    width: 200px;

    input[type=text] {
        padding: 10px 45px 10px 10px;
        border-radius: 20px;
    }
}
